@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
  margin: 0;
  padding: 0;
}

body {
  background: #ffffff;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.wrapper {
  width: 1280px;
  margin: 50px auto;
}

header{
  position: relative;
}

header .logo {
  font-weight: 600;
  font-size: 20px;
}

header ul.nav{
  float:right;
  list-style: none;
}

header ul.nav li{
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 0.5s ease;
}

header ul.nav li:hover{
  opacity: 0.5;
}

header .shop-card-button{
  float: right;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop-card-button:hover,
header .shop-card-button.active{
  color:#dc3d3d;
  transform: scale(1.5);
}

header .shop-card{
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 4px 5px 15px -7px #606060;
  box-shadow: 4px 5px 15px -7px #606060;
  z-index:1000;

  padding: 20px;
  padding-bottom: 0px;
}

header .shop-card .item{
  width: 100%;
  float: left;
  margin-bottom: 20px;
}

header .shop-card .item img{
  width: 70px;
  float: left;
  margin-right: 20px;
}

header .shop-card .item h2{
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop-card .item p{
  color: #797979;
  font-weight: 600;
}

header .shop-card .item .delete-icon{
  color: #ca5252;
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop-card .item .delete-icon:hover{
  color: #d83030 ;
  transform: scale(1.5);
}

header .shop-card .summa{
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}
header .shop-card .nothing{
  margin-bottom: 20px;
}
header .presentation {
  margin: 50px 0;
  background: url('img/shop-background.jpg') no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after {
  color: white;
  content: 'Поринь в світ відеоігор разом з Game Store';
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: #ffffff;
}

header .presentation::before {
  color: white;
  content: 'Низькі ціни - зручний сервіс';
  position: absolute;
  top: 300px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #ffffff;
}



.categories div{
   display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}

.categories div:hover{
  border-color: silver;
  transform: scale(1.1);
}

.full-item{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0, 0.8);
  z-index: 9999;
  overflow: auto;
}

.full-item > div{
  width: 600px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: white;
  border-radius: 9px;
}

main{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item{
  max-width: 305px;
  margin-bottom: 50px;
  background: #f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
}

main .item img, .full-item img {
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}

main .item img:hover, .full-item img:hover{
  transform:  scale(1.05);
}

main h2, .full-item h2,
main p, .full-item p,
main b, .full-item  b{
  margin: 10px 20px;
  color: #333;
}

main b, .full-item b{
  color: #5fa36a;
}

main .add-to-cart,
.full-item .add-to-cart{
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
  transition:  transform 500ms ease;
}

main .add-to-cart:hover,
.full-item .add-to-cart:hover{
  transform: scale(1.5) translateY(-5px);
}

footer{
  text-align: center;
  margin-top: 100px;
}

i {
  border: solid grey;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}

.left {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.close-btn{
  background: #fafafa;
  border: 1px solid grey;
  border-radius: 20px;
  font-weight: 400;
  font-size: 15px;
  margin-bottom: 30px;
  cursor: pointer;
  padding: 10px 20px;
  text-align: center;
  transition: all 500ms ease;
}

.close-btn:hover{
  background-color: lightgrey;

}

.item h2, p{
  cursor: pointer;
}

.wrapper {
  width: 1440px;
  margin: 30px auto 0px auto;
}

@media (min-width: 1220px) and (max-width: 1440px) {
  .wrapper {
    width: 1220px;
    margin: 30px auto 0px auto;
  }
}


@media (min-width: 940px) and (max-width: 1220px) {
  .wrapper {
    width: 940px;
    margin: 30px auto 0px auto;
  }
}
@media (min-width: 800px) and (max-width: 940px){
  .wrapper{
    width: 800px;
    margin: 30px auto 0px auto;
  }

  main{
    justify-content: space-around;
  }
}

@media (min-width: 600px ) and (max-width: 800px){
  .wrapper{
    width: 600px;
    margin: 30px auto 0px auto;
  }

  main{
    justify-content: space-around;
  }
  .full-item > div{
    width: 450px;
  }
}

@media (min-width: 480px ) and (max-width: 600px){
  .wrapper{
    width: 480px;
    margin: 30px auto 0px auto;
  }
  main{
    justify-content: space-around;
  }
  .nav{
    display: none;
  }

  .full-item > div{
    width: 450px;
  }

}

@media (min-width: 320px ) and (max-width: 480px){
  .wrapper{
    width: 310px;
    margin: 30px auto 0 auto;
  }
  main{
    justify-content: space-around;
  }
  .nav{
    display: none;
  }

  header .presentation::before{
    top: 60%;
    font-size: 15px;
  }

  header .presentation::after{
    top: 30%;
    width: 200px;
    font-size: 20px;
  }
  header .shop-card{
    width: 280px;
  }

  .full-item > div{
    width: 280px;
  }
}